import { Component, h, Element, Prop } from '@stencil/core';

@Component({
  tag: 'ks-order-heading',
})
export class OrderHeading {

    @Prop() heading: string;
    @Prop() link: string;

    @Element() root: HTMLElement;

    componentDidLoad(){
        this.root.style.display = "block";
    }

    render() {
        return[
            <div style={{marginBottom: "20px"}} class="uk-flex uk-flex-middle uk-flex-between">
                <div class="uk-text-bold ks-text-decorated" style={{fontSize: "22px", textTransform: "uppercase"}}>
                    {this.heading}
                </div>
                <a href={this.link} class="uk-link-text"><span uk-icon="file-edit"></span></a>
            </div>
        ];
    }
}

